<script>
import { Swiper, SwiperSlide } from "swiper/vue";
import imagesLoaded from "imagesloaded/imagesloaded.pkgd";

export default {
  name: "App",
  components: {
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      sampleData: [
        {
          img: require("@/assets/flower-shop.png"),
          imgBackground: require("@/assets/flower-shop-bgc.png"),
          url: "pagination/flower-shop/index.html#/",
          sitename: "Flower Shop",
          sitetag: ["Vue CLI", "anime.js", "Swiper", "Masonry"],
          description: "電商平台(無後端)。",
        },
        {
          img: require("@/assets/styeeingS.png"),
          imgBackground: require("@/assets/styeeingS-bgc.png"),
          url: "https://styeeings.herokuapp.com/",
          sitename: "StyeeingS",
          sitetag: ["Nuxt.js", "libraries"],
          description: "CSS libraries.",
        },
        {
          img: require("@/assets/crowd-project.png"),
          imgBackground: require("@/assets/crowd-project-bgc.png"),
          url: "https://crowd-project.herokuapp.com/",
          sitename: "Crowd Project",
          sitetag: ["Vue CLI", "Firebase"],
          description:
            "即時通訊實驗項目。\n(展示帳戶: 123456 展示密碼: 12345678)",
        },
        {
          img: require("@/assets/styssing.png"),
          imgBackground: require("@/assets/styssing-bgc.png"),
          url: "pagination/styssing/index.html#/button",
          sitename: "Styssing(Beta)",
          sitetag: ["Vue CLI", "Only PC", "Simonwep/pickr"],
          description: "視覺化操作界面，快速創建和測試基本按鈕。",
        },
        {
          img: require("@/assets/theater.png"),
          imgBackground: require("@/assets/theater-bgc.png"),
          url: "pagination/theater/index.html#/",
          sitename: "Theater",
          sitetag: ["Animation", "Transition", "Vue CLI", "Only PC"],
          description: "Transition art.",
        },
        {
          img: require("@/assets/thinking.png"),
          imgBackground: require("@/assets/thinking-bgc.png"),
          url: "pagination/thinking/index.html",
          sitename: "Thinking",
          sitetag: ["Animation", "locomotive-scroll", "anime.js"],
          description: "動畫實驗網站。",
        },
        {
          img: require("@/assets/financial-management.png"),
          imgBackground: require("@/assets/financial-management-bgc.png"),
          url: "pagination/financial-management/index.html#/list",
          sitename: "Financial Management",
          sitetag: ["Only PC", "Tool", "Vue.js", "Chart.js"],
          description: "電腦版理財網站。",
        },
        {
          img: require("@/assets/cube-shop.png"),
          imgBackground: require("@/assets/cube-shop-bgc.png"),
          url: "pagination/cube-shop/index.html",
          sitename: "Cube Shop",
          sitetag: ["Animation", "Transition", "anime.js", "three.js"],
          description: "動畫實驗網站。",
        },
        {
          img: require("@/assets/layout-b01.png"),
          imgBackground: require("@/assets/layout-b01-bgc.png"),
          url: "pagination/layout-b01/index.html",
          sitename: "Layout B01",
          sitetag: ["Layout"],
          description: "版式 基礎版式 B01 號",
        },
      ],
      sitename: null,
      sitetag: null,
      description: null,
      currentSitePoint: null,
      isLock: false,
      isLoading: false,
      isPreload: true,
      counter: 0,
    };
  },
  methods: {
    lockSample({ url, sitename, sitetag, description }, index) {
      if (this.isLoading) return;

      const screenItem = document.querySelectorAll(".screen-item");
      const screen = document.querySelectorAll(".screen");

      for (const element of screen) {
        if (element.classList.contains("focus-screen")) {
          element.classList.remove("focus-screen");
        }
      }
      for (const element of screenItem) {
        if (element.classList.contains("focus-screen-item")) {
          element.classList.remove("focus-screen-item");
        }
      }

      screenItem[index].classList.add("focus-screen-item");
      screen[index].classList.add("start-up-screen");
      setTimeout(() => {
        screen[index].classList.remove("start-up-screen");
        screen[index].classList.add("focus-screen");
      }, 300);

      this.isLock = true;
      this.isLoading = true;
      this.currentSitePoint = url;
      this.sitename = sitename;
      this.sitetag = sitetag;
      this.description = description;
      setTimeout(() => {
        this.isLoading = false;
      }, 300);
    },
    gotoSite() {
      window.open(this.currentSitePoint);
    },
    goback() {
      const screenItem = document.querySelectorAll(".screen-item");
      const screen = document.querySelectorAll(".screen");

      for (const element of screen) {
        if (element.classList.contains("focus-screen")) {
          element.classList.remove("focus-screen");
          element.classList.remove("start-up-screen");
        }
      }
      for (const element of screenItem) {
        if (element.classList.contains("focus-screen-item")) {
          element.classList.remove("focus-screen-item");
        }
      }

      this.currentSitePoint = null;
      this.sitename = null;
      this.sitetag = null;
      this.description = null;
      this.isLock = false;
    },
    preload() {
      const exhibitionImg = document.querySelectorAll(".exhibition-img");
      const energy = document.querySelector(".energy");

      imagesLoaded(".screen-item", { background: true }, () => {
        this.counter += 30;
        energy.style.width = `${this.counter}%`;
      });
      imagesLoaded(".screen", { background: true }, () => {
        this.counter += 30;
        energy.style.width = `${this.counter}%`;
      });

      for (const element of exhibitionImg) {
        element.onload = () => {
          this.counter += 5;
          energy.style.width = `${this.counter}%`;
        };
      }
    },
    observeActivate() {
      const exhibitionItem = document.querySelectorAll(".exhibition-item");
      let intersectionObserver = new IntersectionObserver((entries) => {
        if (
          entries[0].isIntersecting ||
          (!entries[0].isIntersecting && entries[0].boundingClientRect.x <= 0)
        ) {
          this.offArrow();
        } else {
          this.openArrow();
        }
      });
      intersectionObserver.observe(exhibitionItem[exhibitionItem.length - 1]);
    },
    offArrow() {
      const sample = document.querySelector(".sample");
      sample.classList.add("no-arrow");
    },
    openArrow() {
      const sample = document.querySelector(".sample");
      sample.classList.remove("no-arrow");
    },
  },
  watch: {
    counter(val) {
      if (val >= 100) {
        setTimeout(() => {
          this.isPreload = false;
        }, 200);
      }
    },
  },
  mounted() {
    this.preload();
    if ("IntersectionObserver" in window) {
      this.observeActivate();
    } else {
      this.offArrow();
    }
  },
};
</script>

